.formControl {
    position: relative;
}

.formControl.error input,
.formControl.error textarea {
    border-color: red;
}

.formControl.error div {
    color: red;
    background-color: black;
    padding: 10px;
    position: absolute;
    top: 0;
    left: calc(100% + 30px);
    z-index: 1;
    max-width: 1000px;
    width: fit-content;

}

.formControl.error div:before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: -18px;
    z-index: 1;
    width: 0;
    height: 0;
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
    border-right: 18px solid #000;
}

.formControl.errorRight div {
    color: red;
    background-color: black;
    padding: 10px;
    position: absolute;
    top: 0;
    right: calc(100% + 30px);
    z-index: 1;
    max-width: 1000px;
    width: fit-content;

}

.formControl.errorRight div:before {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -18px;
    z-index: 1;
    width: 0;
    height: 0;
    border-top: 12px solid transparent;
    border-bottom: 12px solid transparent;
    border-left: 18px solid #000;
}

.formSummaryError {
    border: red 1px solid;
    padding: 5px;
    color: firebrick;
}